import React, { useEffect, useRef, useContext} from 'react';
import { useInView } from 'react-intersection-observer';
// context
import { StateContext } from '../../Root';
// constants
import { CONTACT_ME } from '../../../constants/state';
import { FOCUS_CHANGE, FOCUS_NEXT, FOCUS_PREV } from '../../../constants/state';

const ContactMe = () => {
  const { ref, inView, entry } = useInView({
    initialInView: false
  });

  const {state, dispatch} = useContext(StateContext);

  useEffect(() => {
    if(!inView) dispatch({ type: FOCUS_NEXT });
  }, [inView]);

  return (
    <div ref={ref} id='contactme'>
      <h2>Contact Me</h2>     
    </div>
  );
};

export default ContactMe;
