import React from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
  max-width: 800px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 48px;
}
`;

const ProjectsGrid = ({ children }) => {
  return (
    <Wrapper>
      {children}
    </Wrapper>
  );
};

export default ProjectsGrid;
