import React, { useEffect, useRef, useContext} from 'react';
import styled from 'styled-components';
import { useInView } from 'react-intersection-observer';
// context
import { StateContext } from '../../Root';
// constants
import { WHO_AM_I } from '../../../constants/state';
import { FOCUS_CHANGE, FOCUS_NEXT, FOCUS_PREV } from '../../../constants/state';
// assets
import myPicture from '../../../assets/WhoAmI/me.jpg';

const Wrapper = styled.div`
  article {
    img {
      border: 6px solid ${props => props.color};
      max-width: 300px;
    }
  }
`;

const WhoAmI = () => {
  const { ref, inView, entry } = useInView({
    initialInView: true,
  });

  const {state, dispatch} = useContext(StateContext);

  useEffect(() => {
    if(!inView) dispatch({ type: FOCUS_NEXT });
  }, [inView]);

  return (
    <Wrapper ref={ref} id='whoami' color={state.color}>
      <h2>Who Am I?</h2>
      <article>
        <img src={myPicture} alt='my portrait' ></img>
        <p>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nulla laoreet purus a cursus sodales. Integer eu diam rhoncus enim auctor posuere. Aliquam interdum neque et pulvinar ornare. Cras lobortis orci et enim elementum, at pulvinar felis sollicitudin. Pellentesque molestie metus orci, non lobortis libero sodales et. Donec convallis lorem quis accumsan imperdiet. Quisque finibus felis in quam interdum sollicitudin. Suspendisse in urna et nisl efficitur consequat in ac purus. Donec efficitur metus nulla, non sagittis lorem eleifend in. Praesent ac elementum quam, a mollis lacus. Praesent sit amet ante a ex consequat ullamcorper ac ac eros. Mauris in auctor mi, et vestibulum mi. Praesent vitae sapien mauris.
        </p>
      </article>
    </Wrapper>
  );
};

export default WhoAmI;
