import React, { useEffect, useRef, useContext } from 'react';
import styled from 'styled-components';
import { useInView } from 'react-intersection-observer';
// constants
import { SKILLS } from '../../../constants/state';
import { FOCUS_CHANGE, FOCUS_NEXT, FOCUS_PREV } from '../../../constants/state';
// context
import { StateContext } from '../../Root';
// components
import SkillsProgress from './SkillsProgress';


const Skills = () => {
  const { ref, inView, entry } = useInView({
    initialInView: false,
  });
  const scrollAreaRef = useRef();

  const {state, dispatch} = useContext(StateContext);

  useEffect(() => {
    if(!inView) dispatch({ type: FOCUS_NEXT });
  }, [inView]);
  return (
    <div ref={ref} id='skills'>
      <h2>Skills</h2>
      <article>
        <SkillsProgress />
        <h3>Other</h3>
        <ul>
          <li>C++ (fundamentals)</li>
          <li>C# (fundamentals)</li>
          <li>Object Oriented Programming</li>
          <li>jQuery</li>
        </ul>
      </article> 
    </div>
  );
};

export default Skills;
