import React, { useReducer, useContext } from 'react';
import styled from 'styled-components';
import GlobalStyles from './globalStyles';
// constants
import { WHO_AM_I_COLOR, EDUCATION_COLOR, SKILLS_COLOR, PORTFOLIO_COLOR, CONTACT_ME_COLOR } from '../constants/colors';
import { WHO_AM_I, EDUCATION, SKILLS, PORTFOLIO, CONTACT_ME } from '../constants/state';
import { FOCUS_CHANGE, FOCUS_NEXT, FOCUS_PREV, SELECTION_CHANGE } from '../constants/state';
// components
import Sidebar from './Sidebar';
import Content from './Content';


// state

const elementsOrdered = [
  {
    element: WHO_AM_I,
    color: WHO_AM_I_COLOR
  },
  {
    element: EDUCATION,
    color: EDUCATION_COLOR
  },
  {
    element: SKILLS,
    color: SKILLS_COLOR
  },
  {
    element: PORTFOLIO,
    color: PORTFOLIO_COLOR
  },
  {
    element: CONTACT_ME,
    color: CONTACT_ME_COLOR
  }
];

const initialState = {
  elementChosen: null,
  elementInFocus: elementsOrdered[0].element,
  elementInFocusNumber: 0,
  color: elementsOrdered[0].color
};

const reducer = (state = initialState, action) => {
  if(action.type === FOCUS_CHANGE) {
    switch(action.payload) {
      case WHO_AM_I:
        return {
          ...state,
          elementInFocus: WHO_AM_I,
          color: WHO_AM_I_COLOR
        };
      case EDUCATION:
        return {
          ...state,
          elementInFocus: EDUCATION,
          color: EDUCATION_COLOR
        };
      case SKILLS:
        return {
          ...state,
          elementInFocus: SKILLS,
          color: SKILLS_COLOR
        };
      case PORTFOLIO:
        return {
          ...state,
          elementInFocus: PORTFOLIO,
          color: PORTFOLIO_COLOR
        };
      case CONTACT_ME:
        return {
          ...state,
          elementInFocus: CONTACT_ME,
          color: CONTACT_ME_COLOR
        };
      default:
        return state;
    }
  }
  else if(action.type === FOCUS_NEXT) { 
    let newNum = state.elementInFocusNumber + 1;
    if(newNum > 4) newNum = 0;
    return {
      ...state,
      elementInFocus: elementsOrdered[newNum].element,
      elementInFocusNumber: newNum,
      color: elementsOrdered[newNum].color
    } 
  } 
  else if(action.type === SELECTION_CHANGE) {
    const color = elementsOrdered.find(elem => elem.element === action.payload).color;
    return {
      ...state,
      elementChosen: action.payload,
      color: color
    }
  }
  else return state;
};

// context

export const StateContext = React.createContext();

// style

const RootWrapper = styled.div`
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 4fr;
`; 


// component

const Root = () => {
  const [state, dispatch] = useReducer(reducer, initialState);

  return (
    <StateContext.Provider value={{ state, dispatch }}>
      <GlobalStyles />
      <RootWrapper>
        <Sidebar />
        <Content />
      </RootWrapper>
    </StateContext.Provider>
  );
};

export default Root;
